export default{
	data(){
		return{
			nickeName:''
		}
	},
	methods:{
		subName(){
			if(this.nickeName==''){
				this.$api.tip('请输入您的名称!')
			}else{
				let parmas={
					uid:getApp().globalData.uid,
					nickeName:this.nickeName
				}
				this._reqw.ipost(parmas,'edmitPersonInfo').then(res=>{
					res.result==0?(this.$api.tip('修改成功!'),this.$api.prePage().dataObject.nickName=this.nickName,this.$api.back()):this.$api.tip(res.resultNote)
				}).catch(err=>{})
			}
			
		}
	}
}
